.box29{
    width: 32%;
    height:500px;
    border: 1px solid #dadada;
    border-radius: 15px;
    padding: 15px 0px;
    overflow-x: scroll;

    .tableData {
        width: 100%; /* Set table width */
        table-layout: fixed; /* Ensures that all columns have the same width */
    }
    
    .tableData th {
        height: 20px !important; /* Increase the cell height */
        padding: 10px; /* Adjust padding to make the cells more spacious */
        text-align: center; /* Optional: Align text to the center */
    }
    .tableData tr {
        height: 40px !important; /* Ensure the row height is applied */
    }
    
    .tableData th {
        width: 20%; /* Set a fixed width for each column */
    }
    
    .tableData td {
        width: 20%; /* Set a fixed width for each column */
    }
}
