.AthDetails-box1 {
    border: var(--border);
    padding: 1.6rem 2.4rem;
    border-radius: 1rem;
    width: 92% !important;
    margin: 0px 20px;
    .AthDetails-head {
      // padding: 1.6rem 2.4rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
  
      .dataProcess-date {
        //   padding: 1.6rem 2.4rem;
        color: var(--color-dark);
        font-size: 1.4rem;
        display: flex;
        align-items: center;
        gap: 1rem;
        svg {
          fill: var(--color-dark);
        }
  
        .AthDetails-box-title {
          font-size: larger;
          font-weight: 900;
        }
      }
  
      .portfolio {
        display: flex;
        align-items: center;
        color: var(--color-primary) !important;
  
        .add-portfolio {
          font-size: small;
          color: var(--color-primary) !important;
        }
      }
    }
  
    .AthDetails-body {
      margin-top: 4rem;
      display: flex;
      align-items: center;
      gap: 3rem;
  
      .AthDetails-body-box {
        padding: 0.4rem 0.8rem;
        border-radius: 0.4rem;
        background-color: rgba(128, 128, 128, 0.329);
        color: var(--color-dark);
      }
  
      .AthDetails-body-icons {
        .up-down {
          display: flex;
          align-items: center;
          svg {
            fill: var(--color-primary) !important;
            font-size: larger !important;
          }
  
          .up-down-text {
            font-size: small;
            color: var(--color-primary);
          }
        }
      }
    }
  
    .AthDetails-Data-body {
      display: flex;
      gap: 2rem;
      margin-top: 2rem;
  
      .market-data {
        font-size: small;
        color: gray;
      }
      .market-cap {
        color: var(--color-dark);
        font-size: small;
      }
    }
  }